import Component from "../../Base/Component";
import DropdownButton from "../DropdownButton/DropdownButton";
import DropdownMenu from "../DropdownMenu/DropdownMenu";
export default class DropdownComponent extends Component {
    constructor(element, config) {
        super(element, config);
        // * Object Properties and Methods
        this.transition = "none";
        if (config)
            this.transition = config.transition ? config.transition : "none";
        this.button = new DropdownButton(this.element.querySelector("button"), { parent: this });
        this.menu = new DropdownMenu(this.element.querySelector(".menu"), { parent: this });
    }
    static init(selector, config) {
        super.init(selector, config);
        document.addEventListener("keydown", (event) => {
            if (event.key === "Escape") {
                this.closeAll();
            }
        });
        document.addEventListener("click", (event) => {
            if (!this.isInsideDropdown(event.target)) {
                this.closeAll();
            }
        });
    }
    static closeAll() {
        for (const dropdown of this.instances) {
            dropdown.close();
        }
    }
    static isInsideDropdown(clickedElement) {
        let value = false;
        let i = 0;
        while (value === false && i < this.instances.length) {
            const dropdown = this.instances[i];
            value = dropdown.element.contains(clickedElement);
            i++;
        }
        return value;
    }
    get isOpen() {
        return this.element.dataset["state"] === "open";
    }
    set isOpen(value) {
        if (value) {
            this.element.dataset["state"] = "open";
        }
        else {
            this.element.removeAttribute("data-state");
        }
    }
    toggleOpen() {
        this.isOpen = !this.isOpen;
    }
    close() {
        this.isOpen = false;
    }
}
// * Static Properties and Methods
DropdownComponent.className = "dropdown";
